import React, { useContext } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import { faImage, faFilePdf } from '@fortawesome/free-solid-svg-icons';

const Context = React.createContext();
const truncValues = {
    title: 20,
    description: 700
};

function getIcon(key) {
    switch(key) {
        case 'github':
            return faGithub;
        case 'image':
            return faImage;
        case 'pdf':
            return faFilePdf;
        default:
            return;
    }
}

function trunc(string, n, useWordBoundary) {
    if (string.length <= n) { return string; }
    let subString = string.substr(0, n-1);
    return (useWordBoundary
            ? subString.substr(0, subString.lastIndexOf(' '))
            : subString) + '...';
}

function ProjectText() {
    const project = useContext(Context);

    return (
        <div className={`project-text ${project.textAlign}`}>
            <div className='project-title'>{trunc(project.title, truncValues.title, true)}</div>
            <div>{trunc(project.description, truncValues.description, true)}</div>
        </div>
    )
}

function ProjectImages() {
    const project = useContext(Context);

    return (
        <div className={`project-inner ${project.type}-${project.images.length} ${project.textAlign}`} >
            <div style={{background: project.color}} >
                {project.images.map((image, key) => {
                    return <img src={image} alt='' key={key} />
                })}
            </div>
            <div className='project-links'>
                {project.links.map((link, key) => {
                    let type = link.split(/:(.+)/)[0],
                        url = link.split(/:(.+)/)[1];

                    return <a key={key} href={url} download={type === 'pdf'}>
                        <FontAwesomeIcon icon={getIcon(type)} size="2x"/>
                    </a>;
                })}
            </div>
        </div>
    )
}

export default function Project(props) {
    const project = props.project;

    if (
        (project.type === 'landscape' && project.images.length !== 3 && project.images.length !== 4)
        || (project.type === 'portrait' && project.images.length !== 5 && project.images.length !== 6)
    ) {
        return (
            <div className='error'>
                <h2>Couldn't render project: The type with the amount of images is not supported.</h2>
                <h4>With type 'landscape' there can be 3 or 4 images</h4>
                <h4>With type 'portrait' there can be 5 or 6 images</h4>
                <h4>Current type and amount: {project.type} with {project.images.length} images</h4>
            </div>
        )
    }

    return (
        <div className='project-container'>
            <div className='full-container'>
                <Context.Provider value={project}>
                    <ProjectText />
                    <ProjectImages />
                </Context.Provider>
            </div>
        </div>
    )
}
