import React from 'react';

function toggleDropdown() {
    document.getElementById('header').classList.toggle('is-active');
    document.getElementById('menu').classList.toggle('is-active');
    document.getElementById('dropdown').classList.toggle('is-active');
}

function handleLinkClick(link, toggle) {
    if (toggle) toggleDropdown();

    document.getElementById(link).scrollIntoView({behavior: 'smooth'});
}

function switchTheme() {
    document.getElementById('app').classList.toggle('dark');
}

export default function Header() {

    return (
        <div>
            <div id='header'>
                <div onClick={() => handleLinkClick('info')} id='header-title'>Lucas Bos</div>
                <div onClick={() => handleLinkClick('info')}>Over mij</div>
                <div onClick={() => handleLinkClick('vaardigheden')}>Vaardigheden</div>
                <div onClick={() => handleLinkClick('projecten')}>Projecten</div>
                <div onClick={() => handleLinkClick('contact')}>Contact</div>
                <div onClick={toggleDropdown} id='menu' className='hamburger hamburger--slider'>
                    <div className='hamburger-box'>
                        <div className='hamburger-inner'></div>
                    </div>
                </div>
                <div id='theme-switcher'>
                    <label className='switch'>
                        <input onClick={switchTheme} type='checkbox' />
                        <span className='slider round' />
                    </label>
                </div>
            </div>
            <div id='dropdown'>
                <div onClick={() => handleLinkClick('info', true)}>Over mij</div>
                <div onClick={() => handleLinkClick('vaardigheden', true)}>Vaardigheden</div>
                <div onClick={() => handleLinkClick('projecten', true)}>Projecten</div>
                <div onClick={() => handleLinkClick('contact', true)}>Contact</div>
            </div>
        </div>
    )
}